<template>
    <div class="equipmentManagement">
        <div class="search">
            <el-input placeholder="请输入预警班级" v-model.trim="search.keyword" clearable size="small"></el-input>
            <!-- <el-input placeholder="请输入设备所在位置" v-model="search.address" clearable size="small"></el-input> -->
            <el-date-picker v-model="search.warn_date" format='yyyy-MM-dd' value-format="yyyy-MM-dd" size="small" clearable type="date" placeholder="请选择预警日期">
            </el-date-picker>
            <el-button size="small" type="primary" plain icon="el-icon-search" @click="serchList()">查询</el-button>
        </div>
        <div class="home_table">
            <el-table :data="tableData" border element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" size="medium" style="width: 100%">
                <template v-for="(item, index) in tableHead">
                    <el-table-column :prop="item.prop" align="center" :label="item.title" :key="index" v-if="item.prop=='time'" :min-width="item.minWidth">
                        <template slot-scope="scope">
                            {{scope.row.create_time+' '+scope.row.week_name}}
                        </template>
                    </el-table-column>
                    <el-table-column :prop="item.prop" align="center" v-else :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true"></el-table-column>
                </template>
                <el-table-column prop="address" label="操作" fixed="right" width="120">
                    <template slot-scope="scope">
                        <el-button @click="handle(scope.row)" type="text" v-if="scope.row.todo_status==0" size="small">处理</el-button>
                        <el-button @click="check(scope.row)" type="text" v-if="scope.row.todo_status==1" size="small">查看</el-button>
                        <el-button @click="delBtn(scope.row.id)" type="text" size="small" style="color: #ff2b2b">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="pagination">
            <el-pagination background :current-page.sync="page" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="prev, pager, next, sizes,total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
        </div>
        <!-- 预警处理 -->
        <el-dialog title="预警处理" :visible.sync="dialogVisible" v-dialogDrag width="700px" :close-on-click-modal="false" @close='handleClose'>
            <el-form :model="formData" ref="formData" :rules="rule" label-width="110px">
                <el-form-item label="日期">
                    {{formData.create_time+' '+formData.week_name}}
                </el-form-item>
                <el-form-item label="预警班级">
                    {{formData.class_name}}
                </el-form-item>
                <el-form-item label="班主任">
                    {{formData.manage_name}}
                </el-form-item>
                <el-form-item label="病假人数">
                    {{formData.sick_count}}
                </el-form-item>
                <el-form-item label="病假人员">
                    {{formData.student_name}}
                </el-form-item>
                <el-form-item label="预警处理" prop="remark">
                    <el-input type="textarea" autosize maxlength="200" show-word-limit v-model="formData.remark" placeholder="请输入处理情况"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="submit">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="查看预警" :visible.sync="dialogVisiblecheck" v-dialogDrag width="700px" :close-on-click-modal="false" @close='dialogVisiblecheck = false'>
            <el-descriptions :column='1' border :labelStyle='{"width":"15%"}'>
                <el-descriptions-item label="预警班级">{{formData.class_name}}</el-descriptions-item>
                <el-descriptions-item label="班主任">{{formData.manage_name}}</el-descriptions-item>
                <el-descriptions-item label="病假人数">{{formData.sick_count}}</el-descriptions-item>
                <el-descriptions-item label="病假人员">{{formData.student_name}}</el-descriptions-item>
                <el-descriptions-item label="日期">{{formData.create_time+' '+formData.week_name}}</el-descriptions-item>
                <el-descriptions-item label="处理结果">{{formData.todo_status_name}}</el-descriptions-item>
                <el-descriptions-item label="班主任确认">{{formData.confirm_status_name}}</el-descriptions-item>
                <el-descriptions-item label="预警处理">{{formData.remark}}</el-descriptions-item>
            </el-descriptions>
        </el-dialog>
    </div>
</template>

<script>
import {
    hashApi,
    warningProcessingList,
    warningProcessingInfo,
    warningProcessingHandle,
    warningProcessingDel,
} from '@/api/index';
export default {
    data() {
        return {
            total: 0,
            page: 1,
            pageSize: 15,
            tableHeight: window.innerHeight - 180,
            dialogVisible: false,
            dialogVisiblecheck: false,
            title: '',//新增编辑标题
            search: {
                warn_date: '',
                keyword: '',
            },
            formData: {
                remark: '',
            },
            weekBtn: [],
            tableHead: [
                { title: '序号', prop: 'id', minWidth: '100', },
                { title: '预警班级', prop: 'class_name', minWidth: '120', },
                { title: '班主任', prop: 'manage_name', minWidth: '120', },
                { title: '病假人数', prop: 'sick_count', minWidth: '180', },
                { title: '预警时间', prop: 'time', minWidth: '160', },
                { title: '处理结果', prop: 'todo_status_name', minWidth: '150', },
                { title: '班主任确认', prop: 'confirm_status_name', minWidth: '100', },
            ],
            tableData: [],
            rule: {
                remark: [
                    { required: true, message: '请输入处理情况', trigger: 'blur' },
                ],
            },
            deviceType: [],//设备类型列表
            hash: '',
            week: [
                { id: '1', name: '星期一', },
                { id: '2', name: '星期二', },
                { id: '3', name: '星期三', },
                { id: '4', name: '星期四', },
                { id: '5', name: '星期五', },
                { id: '6', name: '星期六', },
                { id: '0', name: '星期天', },
            ],
            // cities: ['上海', '北京', '广州', '深圳'],
        };
    },
    mounted() {
        this.getList();
        this.getHash();
    },
    methods: {
        serchList() {
            this.page = 1;
            this.pageSize = 15;
            this.getList()
        },
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.page = 1;
            this.pageSize = newSize;
            this.getList();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.page = newPage;
            this.getList();
        },
        //处理
        handle(row) {
            this.dialogVisible = true;
            this.resetForm();
            this.getInfo(row.id);
        },
        //查看
        check(row) {
            this.dialogVisiblecheck = true;
            this.getInfo(row.id);
        },
        resetForm() {
            this.formData = {
                remark: '',
            };
            this.$nextTick(() => {
                this.$refs['formData'].resetFields();
            })
        },
        handleClose() {
            this.resetForm()
            this.dialogVisible = false;
        },
        async getInfo(id) {
            const res = await this.axios.post(warningProcessingInfo, { id: id });
            if (res.data.code == 1) {
                this.formData = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async getList() {
            let data = {
                page: this.page,
                pageSize: this.pageSize,
                warn_date: this.search.warn_date,
                keyword: this.search.keyword,
            }
            const res = await this.axios.post(warningProcessingList, data);
            if (res.data.code == 1) {
                this.tableData = res.data.data.data;
                this.total = res.data.data.total;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async getHash() {
            const res = await this.axios.post(hashApi, { page_hash: 'warningProcessing' });
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        submit() {
            this.$refs['formData'].validate(async valid => {
                if (valid) {
                    await this.getHash();
                    let data = {
                        id: this.formData.id,
                        remark: this.formData.remark,
                        hash: this.hash,
                    }
                    const res = await this.axios.post(warningProcessingHandle, data);
                    if (res.data.code == 1) {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'success'
                        });
                        this.dialogVisible = false;
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error'
                        });
                    }
                    this.getList();
                    this.getHash();
                }
            })
        },
        delBtn(id) {
            this.$confirm('是否确定进行删除', '删除提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            })
                .then(() => {
                    const datas = {
                        id: id,
                        hash: this.hash,
                    };
                    const res = this.axios.post(warningProcessingDel, datas).then(res => {
                        if (res.data.code == 1) {
                            this.$message({
                                showClose: true,
                                message: res.data.msg,
                                type: 'error'
                            });
                            if ((this.total - 1) % this.pageSize === 0 && this.total > 1) {
                                this.page -= 1;
                            }
                            this.getList();
                        } else {
                            this.$message({
                                showClose: true,
                                message: res.data.msg,
                                type: 'error'
                            });
                        }
                    });
                    this.getHash();
                })
                .catch(err => {
                    // this.$notify.info({
                    //     title: '取消',
                    //     message: '取消操作'
                    // });
                });
        },
    },
};
</script>

<style lang="scss" scoped>
.search {
    display: flex;
    padding: 12px 25px;
    background: #fff;
    .el-select {
        margin-right: 24px;
    }
    .el-input {
        width: auto;
        margin-right: 24px;
    }
    .el-cascader {
        margin-right: 24px;
    }
    .add {
        flex: 1;
        .btn_add {
            float: right;
        }
        .el-input {
            width: 500px;
        }
    }
}
.el-form-item {
    margin-bottom: 0;
}
</style>


